import { sliderSplideListItem } from '../templates/templateCreator';
import Splide from '@splidejs/splide';

class Slider extends HTMLElement {
  // connectedCallback() {
  //   this.spliderSetting();
  // }

  set value(content) {
    this._content = content;
    this.render();
    this.afterRender();
    // console.log(content)
    new Splide('.splide', {
      type: 'loop',
      autoplay: true,
      interval: 5000,
    }).mount();
    this.spliderSetting();
  }

  set title(title) {
    this._title = title;
  }

  spliderSetting = () => {
    const spliderArrow = document.querySelector('.splide__arrows');
    const splidePagination = document.querySelector('.splide__pagination');
    const widthScreen = window.innerWidth;

    if (widthScreen < 600) {
      spliderArrow.classList.add('!hidden');
      splidePagination.classList.add('!hidden');
    }
  };

  render() {
    this.innerHTML = `
        <section class="splide lg:h-[500px]" aria-label="Splide Basic HTML Example">
          <div class="splide__track w-full h-full md:rounded">
              <ul class="splide__list">
                  
              </ul>
          </div>
      </section>
    `;
  }

  afterRender() {
    const splideList = this.querySelector('.splide__list');

    this._content.slice(0, 5).forEach(async (movie) => {
      splideList.innerHTML += sliderSplideListItem(movie);
    });
  }
}

customElements.define('slider-tag', Slider);
