/* eslint-disable import/named */
/* eslint-disable no-plusplus */

import { suggesionsTemplate } from '../templates/templateCreator';

class Suggestions extends HTMLElement {
  set value(content) {
    this._content = content;
    this.render();
    this.afterRender();
    // console.log(this._content);
  }

  set title(title) {
    this._title = title;
    // console.log(this._title);
  }

  setTitle() {
    return this._title.split('-').map((word) => word.charAt(0).toUpperCase() + word.slice(1)).join(' ');
  }

  async render() {
    this.innerHTML = `
      <div class="suggestion bg-slate-800 p-2 text-slate-50 rounded w-full">
        <div class="title mb-2 px-2">
          <h2 class="font-bold text-lg">${this.setTitle()}</h2>
        </div>
        <div class="suggestions-list flex flex-col gap-2 h-[500px] lg:h-fit lg:max-h-[calc(100vh-240px)]  overflow-y-scroll scrollbar-custom pt-1">

        </div>
      </div>
    `;
  }

  async afterRender() {
    const suggestionList = this.querySelector('.suggestions-list');

    this._content.slice(0, 9).forEach((tv) => {
      suggestionList.innerHTML += suggesionsTemplate(tv, this.setTitle());
      // console.log(tv);
    });
  }
}

customElements.define('suggestion-tag', Suggestions);
